.user {
  display: flex;
  align-items: flex-start;
}

.user__header {
  flex-shrink: 0;
  width: 100px;
  margin-right: 20px;
}

.user__photo {
  width: 100%;
  line-height: 0;
}

.user__photo img {
  width: 100%;
}

.user__btn {
  background-color: green;
  color: #fff;
  border: none;
  box-shadow: none;
  margin-top: 10px;
  width: 100%;
  padding: 10px;
  cursor: pointer;
  text-transform: uppercase;
}

.user__body {
  padding-top: 10px;
}

.user__status {
  margin-bottom: 5px;
}

.user__name {
  margin-bottom: 5px;
  font-weight: bold;
}
.user__city {
  margin-right: 5px;
  font-weight: bold;
}