.header {
  height: 60px;
  width: 100%;
  background-color: green;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  display: flex;
  align-self: center;
  justify-content: center;
  background-color: #ccc;
  height: 100%;
}

.header__logo img {
  height: 100%;
  width: auto;
}